import React, { useEffect, useState } from 'react';
import './Survey.css';
import SwipeableViews from 'react-swipeable-views';
import LinearProgress from '@material-ui/core/LinearProgress'

const surveyResult = {};

const Survey = ({ history }) => {
    const surveyData = [
        {
            description: '문항을 넣어주세요. 그리고 1번과 2번 둘중 하나를 골라주세요.',
            answer: [{
                text: '긍정적인 1번 답입니다.',
                value: 1
            }, {
                text: '부정적인 1번 답입니다.',
                value: 2
            }]
        }, {
            description: '2번째 문항을 넣어주세요. 그리고 1번과 2번 둘중 하나를 골라주세요.',
            answer: [{
                text: '긍정적인 2번 답입니다.',
                value: 1
            }, {
                text: '부정적인 2번 답입니다.',
                value: 2
            }]
        }, {
            description: '3번째 문항을 넣어주세요. 그리고 1번과 2번 둘중 하나를 골라주세요.',
            answer: [{
                text: '긍정적인 3번 답입니다.',
                value: 1
            }, {
                text: '부정적인 3번 답입니다.',
                value: 2
            }]
        }, {
            description: '4번째 문항을 넣어주세요. 그리고 1번과 2번 둘중 하나를 골라주세요.',
            answer: [{
                text: '긍정적인 4번 답입니다.',
                value: 1
            }, {
                text: '부정적인 4번 답입니다.',
                value: 2
            }]
        }, {
            description: '5번째 문항을 넣어주세요. 그리고 1번과 2번 둘중 하나를 골라주세요.',
            answer: [{
                text: '긍정적인 5번 답입니다.',
                value: 1
            }, {
                text: '부정적인 5번 답입니다.',
                value: 2
            }]
        }
    ];

    const pageRate =  100 / surveyData.length;
    const [progress, setProgress] = useState(pageRate);
    const [isLoading, setIsLoading] = useState(true);
    const [currentScreen, setCurrentScreen] = useState(0);

    const btnEnter = (e) => {
        e.target.style.background = '#388e3c';
        e.target.style.color = '#fff';
    }
    const btnLeave = (e) => {
        e.target.style.background = '#eee';
        e.target.style.color = '#000';
    }

    const handleNext = (page, value) => {
        // 페이지에서 누른 버튼 값 으로 결과 저장
        surveyResult[page] = value;

        // 현재 페이지는 바뀌기 전이라 인덱스 0부터 포함하여 +2를 해야함
        if(currentScreen + 2 > surveyData.length) {
            goToResult(surveyResult);
            return;
        }
        setCurrentScreen((curScreen) => curScreen + 1);
        setProgress((currentScreen + 2) * pageRate);
    };

    const handleStepChange = (curScreen) => {
        setCurrentScreen((curScreen));
        setProgress((curScreen + 1) * pageRate);
    };
    
    // 어디 결과 페이지로 갈지 분기
    const goToResult = (surveyResult) => {
        let cntOne = 0;
        let cntTwo = 0;
        Object.values(surveyResult).forEach((val) => {
            if(val == 1) {
                cntOne++;
            } else if (val == 2) {
                cntTwo++;
            }
        });

        // 라우팅
        let route = '';
        if(cntOne === surveyData.length) {
            route = 'onlyOne';
        } else if(cntTwo === surveyData.length) {
            route = 'onlyTwo';
        } else {
            route = 'else';
        }

        history.push({
            pathname: `/result/${route}`,
            state: {
                result: surveyResult,
                cntOne,
                cntTwo,
                isAfterSurvey: true
            }
        });
    };

    useEffect(() => {
        setIsLoading(false);
    }, []);

    return isLoading ? null : (
        <div id="main-container">
            <SwipeableViews 
                enableMouseEvents={true} 
                index={currentScreen}
                onChangeIndex={handleStepChange}
                disabled={true}
                steps={surveyData.length - 1}
                position="static"
                style={{width: '100%', maxWidth: 400, display: 'inline-block'}}>
                
                {surveyData.map((survey, index) => {
                    return (
                        <div key={index}>
                            <div  className="title">Question.{index + 1}</div>
                            <div  className="description">{survey.description}</div>
                            <div  className="button-container">
                                {survey.answer.map((ans, idx) => (
                                    <button 
                                        key={`${index}_${idx}`} 
                                        className="survey-button" 
                                        onMouseEnter={btnEnter} 
                                        onMouseLeave={btnLeave}
                                        onClick={() => handleNext(index + 1, ans.value)} >{idx + 1}. {ans.text}</button>
                                ))}
                            </div>
                        </div>
                    )
                })}
            </SwipeableViews>
            <div></div>
            <div id="progress-container">
            <div id="progress-text-container">{currentScreen + 1} / {surveyData.length}</div>
                <LinearProgress value={progress} variant="determinate" style={{maxWidth: 400, height: 10, borderRadius: 15, colorPrimary: '#fff', barColorPrimary: '#000'}} />
            </div>
        </div>
    );
}

export default Survey;

